html {
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  background-color: #ffffff;
  min-height: 100vh;
}

a {
  text-decoration: none;
}

a:focus, input:focus {
  outline: 2px solid #27b7ca;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  display: block;
  max-width: 100%;
}

body .background {
  margin: 2em;
}
@media (min-width: 1024px) {
  body .background {
    max-width: 1170px;
  }
}
body .header__container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

@media (min-width: 768px) {
  nav {
    max-width: 100%;
    margin: auto;
  }
}

.container {
  max-width: 100%;
  margin: 3.25em auto 1.3em auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.container__title {
  text-align: center;
  font-size: 1.25em;
  font-weight: 700;
  font-style: italic;
  color: #0274b6;
}
.container__form {
  display: flex;
  position: relative;
  margin: 1em;
}
.container .search {
  border: 3px solid #9ecc3c;
  border-radius: 25px 25px;
  text-align: left;
  font-size: 1em;
  max-width: 100%;
  position: relative;
  bottom: 0;
  padding: 0.5em 2.3em;
}
.container .search::placeholder {
  font-style: italic;
  color: #707070;
}
.container .submit {
  border: none;
  position: absolute;
  width: 26px;
  height: 26px;
  bottom: 6px;
  right: 11px;
  cursor: pointer;
}
@media (min-width: 1024px) {
  .container .search {
    padding: 0.5em auto 2.875em;
    text-align: left;
  }
  .container .submit {
    width: 28px;
    height: 28px;
    bottom: 5px;
    right: 13px;
  }
}

.content, .content__error {
  text-align: center;
}
.content .text__valor {
  display: none;
}
.content .text__valor, .content .text__id, .content .text__result, .content .text__data {
  color: #1c3a8d;
  font-size: 1.25em;
  text-transform: capitalize;
  font-weight: 500;
  animation: fadeIn linear 0.5s;
}
.content, .content__error {
  padding-top: 0.2em;
}
@media (min-width: 768px) {
  .content__client, .content__detail {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: row;
  }
  .content__error {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-direction: column;
  }
  .content .text__result {
    order: 2;
  }
  .content .text__data {
    order: 1;
  }
  .content .text__valor {
    display: inline-block;
  }
}

.content__error .text__error__detail, .content__error .text__error__search {
  color: #e21d1d;
  font-size: 1.25em;
  text-transform: capitalize;
  font-weight: 500;
  animation: fadeIn linear 0.5s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}